<template>
    <div class="wrapper">
        <div class="login">
            <div class="input-wrapper">
                <input @changtexte="onchangeUserNumber" class="input" type="text" placeholder="ID" autofocus="true" value=""/>
                <image class="input-img" src="resources/img/login_input_user_img.png"></image>
            </div>
            <div class="input-wrapper">
                <input @changtexte="onchangeUserPassword" class="input" type="password" placeholder="密码" value=""/>
                <image class="input-img" src="resources/img/login_input_pass_img.png"></image>
            </div>
            <div class="input-wrapper">
                <div class="input-login" @click="login">
                    <text class="input-login-text">登录</text>
                </div>
            </div>
            <div class="input-wrapper">
                <text class="input-forget" >忘记密码请联系管理员</text>
                <text class="input-lic">登入表示同意本软件使用协议</text>
            </div>
        </div>
    </div>
</template>

<script>
    let modal = weex.requireModule('modal')
    let stream = weex.requireModule('stream')
    let navigator = weex.requireModule('navigator')
    const storage = weex.requireModule('storage')
    let apis = require('./apis.json')
    let UrlTools = require('./UrlTools.js')
    module.exports = {
        data:{
            userNumber:'',
            userPassword:''
        },
        methods:{
            onchangeUserNumber:function (event) {
                modal.toast({
                  message: '请输入登入账号',
                  duration: 0.3
                })
                this.userNumber = event.value;
            },
            onchangeUserPassword:function (event) {
                modal.toast({
                  message: '请输入登入账号',
                  duration: 0.3
                })
                this.userPassword = event.value;
            },
            /*处理登录*/
            login: function () {
                if(this.userNumber.length < 1){
                    modal.toast({
                      message: '请输入登入账号',
                      duration: 0.3
                    })
                    return
                }
                if(this.userPassword.length < 1){
                    modal.toast({
                      message: '请输入登入密码',
                      duration: 0.3
                    })
                    return
                }
                this.handleLogin()
            },
            handleLogin: function () {
              stream.fetch({
                method: 'GET',
                type: 'json',
                url: apis.login + '/' + this.userNumber + '/' + this.userPassword
              }, res => {
                if(res.ok && res.data && res.data.code === 0) {
                    modal.toast({
                      message: '登入成功',
                      duration: 0.3
                    })
                    storage.setItem('userKey', 'ok')
                    storage.setItem('time', Date.now() + '')
                    console.log(UrlTools.getUrlBase(this.bundleUrl))
                    navigator.pop({
                      animated: false
                    })
                    navigator.push({
                      url: UrlTools.getUrlBase(this.bundleUrl) + 'main.js',
                      animated: true
                    })
                } else {
                    modal.toast({
                      message: '登入失败',
                      duration: 0.3
                    })
                }
              })
            },
            getDate:function(str) {
              try{
                let strd = parseInt(str)
                return new Date(strd)
              } catch(e) {
                return null
              }
            },
            getDays: function(date) {
              return parseInt((Date.now() - date) / (1000 * 60 * 60 * 24))
            }
        },
        mounted: function() {
          storage.getItem('userkey', res => {
            if(res.result === 'success') {
              storage.getItem('time', e => {
                if(e.result === 'success') {
                  let date = this.getDate(e.data)
                  if(date) {
                    if(this.getDays(date) > 7) {
                      storage.removeItem('userkey')
                    } else {
                      storage.setItem('time', Date.now() + '')
                      console.log(UrlTools.getUrlBase(this.bundleUrl))
                      navigator.pop({
                        animated: false
                      })
                      navigator.push({
                        url: UrlTools.getUrlBase(this.bundleUrl) + 'main.js',
                        animated: true
                      })
                    }
                  } else {
                    storage.removeItem('userkey')
                  }
                } else {
                  storage.removeItem('userkey')
                }
              })
            }
          })
        }
    }
</script>

<style>
    .wrapper {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
    .login{
        margin-top: 180px;
    }
    .input-wrapper{
        width: 550px;
        margin-left: 100px;
        margin-right: 100px;
        margin-bottom: 30px;
    }
    .input {
        font-size: 30px;
        height: 80px;
        width: 550px;
        padding-left: 90px;
        padding-top: 15px;
        padding-bottom: 15px;
        border-width:1px;
        border-color: #48c9bf;
        border-radius:10px;
        outline: none;
    }
    .input-img{
        position: absolute;
        top:10px;
        left: 15px;
        width: 60px;
        height: 60px;
    }
    .input-login{
        height: 80px;
        width: 550px;
        background-color: #48c9bf;
        border-radius: 10px;
        margin-top: 40px;
    }
    .input-login-text{
        height: 80px;
        width: 550px;
        text-align: center;
        line-height: 80px;
        color: white;
        font-size: 35px;
    }
    .input-forget{
        position: absolute;
        left: 30px;
        font-size: 20px;
    }
    .input-lic{
        position: absolute;
        right: 30px;
        font-size: 20px;
    }
</style>



